<template>
  <div class="plant-info">
    <p>{{ plant.genus }} - {{ plant.name }}</p>
    <p>Pot Size: {{ plant.potSize }} inches</p>
    <p>Price: ${{ plant.price }}</p>
    <p>Quantity: {{ plantQuantity }}</p>
    <p>Total Price: ${{ plant.price * plantQuantity }}</p>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions } from "vuex";
export default {
  props: ["plantId", "plantQuantity"],
  data() {
    return {
      plant: {},
      error: null
    };
  },
  methods: {
    ...mapActions(["getPlant"]),
    async getPlantInfo() {
      try {
        const res = await axios.get(`/api/v1/plants/${this.plantId}`);
        this.plant = res.data.plant;
      } catch (err) {
        this.error = err;
      }
    }
  },
  async created() {
    try {
      await this.getPlantInfo();
    } catch (err) {
      this.error = err;
    }
  }
};
</script>

<style scoped>
.plant-info {
  margin: 5px;
  padding: 20px;
  background-color: #f8f8f8;
}
</style>