<template>
  <div class="cart">
    <PlantCart
      v-for="(quantity, id) in cart"
      v-bind:key="id"
      v-bind:plantId="id"
      v-bind:plantQuantity="quantity"
    />
    <button type="submit" class="button" v-on:click="purchase">Purchase</button>
  </div>
</template>

<script>
import PlantCart from "@/components/PlantCart";
export default {
  components: {
    PlantCart
  },
  data() {
    return {
      plantsInfo: []
    };
  },
  computed: {
    cart: {
      get: function() {
        return JSON.parse(localStorage.getItem("cart"));
      },
      set: function(cart) {
        return localStorage.setItem("cart", JSON.stringify(cart));
      }
    }
  },
  methods: {
    purchase() {
      localStorage.removeItem("cart");
      this.$router.push({ name: "CheckoutSuccess" });
    }
  },
  async created() {}
};
</script>

<style scoped>
.cart {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 5px;
}

.button {
  height: 24px;
  width: 100px;
  margin: 0 auto;
}
</style>